<template>
    <v-container fluid fill-height style="padding:0;" id="destructure" grid-list-md>
        <Blog :items="items" :time="time" :title="title">
            <div class="blogcontent">
                <v-layout row wrap>
                    <v-flex xs12>
                        <v-hover>
                        <v-card
                            slot-scope="{ hover }"
                            :class="`elevation-${hover ? 12 : 2}`"
                            min-height="200"
                        >
                            <v-card-title>
                            <h3 id="array">解构数组</h3>
                            </v-card-title>
                            <v-divider></v-divider>
                            <v-card-text>
                                <p class=".body-1">1. 数组解构赋值，可以指定默认值</p>
                                <pre class="code">{{pres[0]}}</pre>

                                <p class=".body-1">2. 利用数组解构交换两个变量的值</p>
                                <pre class="code">{{pres[1]}}</pre>

                                <p class=".body-1">3. 可以使用剩余操作符获取剩余的变量数组</p>
                                <pre class="code">{{pres[2]}}</pre>
                            </v-card-text>
                        </v-card>
                        </v-hover>
                    </v-flex>
                </v-layout>

                <v-layout row wrap>
                    <v-flex xs12>
                        <v-hover>
                        <v-card
                            slot-scope="{ hover }"
                            :class="`elevation-${hover ? 12 : 2}`"
                            min-height="200"
                        >
                            <v-card-title>
                            <h3 id="object">解构对象</h3>
                            </v-card-title>
                            <v-divider></v-divider>
                            <v-card-text>
                                <p class=".body-1">1. 对象解构赋值，可以指定默认值</p>
                                <pre class="code">{{pres[3]}}</pre>

                                <p class=".body-1">2. 更换属性名</p>
                                <pre class="code">{{pres[4]}}</pre>

                                <p class=".body-1">3. 可以使用剩余操作符获取剩余的变量对象</p>
                                <pre class="code">{{pres[5]}}</pre>

                                <p class=".body-1">4. 函数参数的解构可以指定一个空的对象作为无参调用的默认值</p>
                                <pre class="code">{{pres[6]}}</pre>
                            </v-card-text>
                        </v-card>
                        </v-hover>
                    </v-flex>
                </v-layout>
            </div>
        </Blog>
    </v-container>
</template>

<script>
import Blog from "../Blog.vue";
export default {
    components: { Blog },
    data() {
        return {
            time: "2019-1-20",
            title: "解构赋值",
            items: [
                {
                    title: "解构数组",
                    target: "#array"
                },
                { divider: true },
                {
                    title: "解构对象",
                    target: "#object"
                },
            ],
            pres: 
[
`
    const arr = [1,2,3];
    let [a, b, , c = 4] = arr;
    // 使用空的逗号就是忽略掉这个值，可以使用=指定默认值
    console.log(a, b, c); // 1 2 4

`,
`
    let a = 1;
    let b = 2;
    [a, b] = [b, a]; // 2 1

`,
`
    const arr = [1, 2, 3];
    let [a, ...b] = arr;
    console.log(a, b); // 1 [2, 3]

`,
`
    const obj = {a: 'aa', b: 'bb'};
    let {a, b, c='cc'} = obj;
    console.log(a, b, c); // aa bb cc

`,
`
    let {a:aa, b:bb, c:cc='cc'} = obj;
    console.log(aa, bb, cc); // aa bb cc

`,
`
    let {a, ...b} = {a: 'aa', b: 'bb', c: 'cc'};
    console.log(a, b); // aa {b: "bb", c: "cc"}
    
`,
`
    function fn({a = 'aa', b = 'bb'}) {
        console.log(a, b);
    }
    fn(); // 报错
    fn({a: 'a'}); // a bb 至少传递一个参数

    // 指定解构默认对象
    function fn({a = 'aa', b = 'bb'} = {}) {
        console.log(a, b);
    }
    fn(); // 不报错 aa bb
`
],
        };
    }
}
</script>   

<style lang="scss">
#destructure {
  .blogcontent {
    padding-top: 20px;
  }
  .code {
    font-size: 12px;
    background-color: #f9f9f5;
    white-space: pre-wrap;
    word-wrap: break-word;
  }
}
</style>

